.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10%;
    background-color: #f5f5f5;
  }
  
  .header h1 {
    font-size: 1.8em;
    font-weight: bold;
  }
  
  .header nav a {
    margin: 0 10px;
    text-decoration: none;
    color: #333;
  }
  
  .header nav a:hover {
    color: #007acc;
  }